@use "vars";

html {
    font-family: 'Mulish', 'Muli', sans-serif;
    font-weight: vars.$font-weight-normal;
}

body {
    display: flex;
    flex-direction: column;

    height: 100vh;
}


// Flexbox shared between header and main.
.content {
    max-width: 180vh;
    margin: 0 auto;
    display: flex;
    flex: 1;
}
.content-row {
    display: flex;
    flex: 1;

    margin: 0 vars.$content-margin;

    display: flex;
    align-items: stretch;
    flex-direction: row;
    justify-content: space-between;
    gap: 3rem;
}

// Individual element layout.
header {
    flex: 0 0 auto;
    height: vars.$header-height;

    .header-left {
        flex: 1 1 100%;
        align-self: baseline;
    }
    .header-center {
        flex: 0 1 auto;
        align-self: baseline;
        text-align: center;

        max-width: vars.$sudoku-size-big;

        h1 {
            font-size: 2rem;
            font-weight: vars.$font-weight-heavy;
            white-space: nowrap;
            margin-bottom: 0.5rem;
        }

        .setter {
            white-space: nowrap;
            /* Maybe this needs a different font size. Not sure. */
        }
    }
    .header-right {
        flex: 1 1 100%;
        align-self: baseline;

        text-align: right;
    }
}

main {
    flex: 1 0 auto;

    display: flex;
    > .content {
        flex: 1;
    }

    .left-panel {
        flex: 1 1 100%;
    }

    .center-panel {
        flex: 0 0 auto;

        /* maintain square aspect ratio */
        width: vars.$sudoku-size-big; height: vars.$sudoku-size-big;
    }

    .right-panel {
        flex: 1 1 100%;
    }
}

@media (max-width: 800px) and (max-aspect-ratio: 5/4),
        (max-width: 600px)
{
    header {
        .header-center {
            flex: 1 1 auto;

            max-width: none;
        }
        .header-left, .header-right {
            display: none;
        }
    }
    main {
        .content-row {
            flex-direction: column;
        }
        .center-panel {
            width: vars.$sudoku-size-small; height: vars.$sudoku-size-small;

            align-self: center;
        }
        .left-panel {
            display: none;
        }
    }
}

footer {
    flex: 0 0 auto;
    height: vars.$footer-height;
    line-height: vars.$footer-height;

    font-size: 0.8rem;
    text-align: center;
}
